
import Vue from "vue";

export default Vue.extend({
  props: {
    time: {
      type: String,
      default: '',
    },
    fileName: {
      type: String,
      default: '',
    },
  },

  data: () => ({
    timeLeft: "00:00:00",
    seconds: 0,
    minutes: 0,
    status: false,
    interval: 0,
    reloadMp3: true
  }),

  created() {
    this.initialValues();
  },

  computed:{
    pathFile(): string {
      return require(`@/assets/audio/${this.$props.fileName}.mp3`)
    }
  },

  methods: {
    formatTime(num: number): string {
      return new Date(num).toISOString().slice(14, 19);
    },
    formatCurrentTime() {
      const seconds = this.seconds;
      this.timeLeft = this.formatTime(seconds * 1000)
    },
    initialValues() {
      const hms = this.$props.time
      const a = hms.split(':')

      this.seconds = (+a[0]) * 60 * 60 + (+a[1]) * 60 + (+a[2]);
      this.formatCurrentTime();
    },
    continueCounting() {
      this.status = true;
      this.counting();
    },
    pauseCounting() {
      this.status = false;
      clearInterval(this.interval);
    },
    resetCounting() {
      this.pauseCounting();
      this.initialValues();
      this.stopNotify()
    },
    counting() {
      this.interval = setInterval(() => {
        if (this.seconds <= 0) {
          this.pauseCounting();
          this.timeLeft = this.formatTime(0) //new Date(0).toISOString().slice(14, 19);
          this.notify();
          return;
        }

        this.seconds--;

        this.formatCurrentTime();
      }, 1000);
    },
    notify(){
      let ref: any = this.$refs[this.$props.fileName];
      ref.play();
    },
    stopNotify(){
      let ref: any = this.$refs[this.$props.fileName];
      ref.pause();
    }
  },

  watch: {
    "$props.time"() {
      this.resetCounting()
    },
    "$props.fileName"() {
      this.reloadMp3 = false

      setTimeout(() => {
        this.reloadMp3 = true
      }, 500)
    },
  },
});
